  },
  created() {
    this.$nextTick(() => {
      this.get_defaultData();
      this.get_data();
    });
  },
  mounted() {},
  methods: {
    tabOnClick(name, title, index) {
      if (name == 0) {
        if (this.insideTabActive_1 == 0) {
          this.$nextTick(() => {
            this.drawLine_xse();
            this.totalSum = this.xse_all;
            this.tqTotalSum = this.tqxse_all;
          });
        } else if (this.insideTabActive_1 == 1) {
          this.$nextTick(() => {
            this.drawLine_nlxse();
            this.totalSum = this.xse_all;
            this.tqTotalSum = this.nlxse_all;
          });
        }
      } else if (name == 1) {
        if (this.insideTabActive_2 == 0) {
          this.$nextTick(() => {
            this.drawLine_mle();
            this.totalSum = this.mle_all;
            this.tqTotalSum = this.tqmle_all;
          });
        } else if (this.insideTabActive_2 == 1) {
          this.$nextTick(() => {
            this.drawLine_nlmle();
            this.totalSum = this.mle_all;
            this.tqTotalSum = this.nlmle_all;
          });
        }
      } else if (name == 2) {
        if (this.insideTabActive_3 == 0) {
          this.$nextTick(() => {
            this.drawLine_xss();
            this.totalSum = this.xss_all;
            this.tqTotalSum = this.nlxss_all;
          });
        } else if (this.insideTabActive_3 == 1) {
          this.$nextTick(() => {
            this.drawLine_nlxss();
            this.totalSum = this.xss_all;
            this.tqTotalSum = this.nlxss_all;
          });
        }
      } else if (name == 3) {
        if (this.insideTabActive_4 == 0) {
          this.$nextTick(() => {
            this.drawLine_kccb();
            this.totalSum = this.kccb_all;
            this.tqTotalSum = this.nlkccb_all;
          });
        } else if (this.insideTabActive_4 == 1) {
          this.$nextTick(() => {
            this.drawLine_nlkccb();
            this.totalSum = this.kccb_all;
            this.tqTotalSum = this.nlkccb_all;
          });
        }
      }
    },
    insideTab_1(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_xse();
          this.totalSum = this.xse_all;
          this.tqTotalSum = this.tqxse_all;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlxse();
          this.totalSum = this.xse_all;
          this.tqTotalSum = this.nlxse_all;
        });
      }
    },
    insideTab_2(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_mle();
          this.totalSum = this.mle_all;
          this.tqTotalSum = this.tqmle_all;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlmle();
          this.totalSum = this.mle_all;
          this.tqTotalSum = this.nlmle_all;
        });
      }
    },
    insideTab_3(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_xss();
          this.totalSum = this.xss_all;
          this.tqTotalSum = this.tqxss_all;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlxss();
          this.totalSum = this.xss_all;
          this.tqTotalSum = this.nlxss_all;
        });
      }
    },
    insideTab_4(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_kccb();
          this.totalSum = this.kccb_all;
          this.tqTotalSum = this.tqkccb_all;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlkccb();
          this.totalSum = this.kccb_all;
          this.tqTotalSum = this.nlkccb_all;
        });
      }
    },
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      // console.log(this.$store.state.defaultDate);
      const time1 = this.formatDate.pastDate(
        this.$store.state.defaultDate + 1,
        "-"
      );
      const time2 = this.formatDate.pastDate(2, "-");
      this.calendar_startDate = time1; //日历默认开始时间
      this.calendar_endDate = time2; //日历默认结束时间
      this.calendar.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar2_startDate = time1; //日历默认开始时间
      this.calendar2_endDate = time2; //日历默认结束时间
      this.calendar2.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar2.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar3_startDate = time1; //日历默认开始时间
      this.calendar3_endDate = time2; //日历默认结束时间
      this.calendar3.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar3.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar4_startDate = time1; //日历默认开始时间
      this.calendar4_endDate = time2; //日历默认结束时间
      this.calendar4.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar4.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.dateArray2 = this.dateArray3 = this.dateArray4 = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportDeptData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          const departmentName = [];
          //销售额
          const theDay_xse = [];
          const contemporary_tqxse = [];
          const contemporary_nlxse = [];

          //毛利额
          const theDay_mle = [];
          const contemporary_tqmle = [];
          const contemporary_nlmle = [];

          //毛利额
          const theDay_xss = [];
          const contemporary_tqxss = [];
          const contemporary_nlxss = [];

          //毛利额
          const theDay_kccb = [];
          const contemporary_tqkccb = [];
          const contemporary_nlkccb = [];
          resultData.forEach(item => {
            departmentName.push(item.code.replace(/\n/g, ""));
            //销售额
            if (item.salesVolume) {
              theDay_xse.push(item.salesVolume);
            } else {
              theDay_xse.push("0");
            }
            if (item.salesVolumeSame) {
              contemporary_tqxse.push(item.salesVolumeSame);
            } else {
              contemporary_tqxse.push("0");
            }
            if (item.nsalesVolumeSame) {
              contemporary_nlxse.push(item.nsalesVolumeSame);
            } else {
              contemporary_nlxse.push("0");
            }

            //毛利额
            if (item.grossMargin) {
              theDay_mle.push(item.grossMargin);
            } else {
              theDay_mle.push("0");
            }
            if (item.grossMarginSame) {
              contemporary_tqmle.push(item.grossMarginSame);
            } else {
              contemporary_tqmle.push("0");
            }
            if (item.ngrossMarginSame) {
              contemporary_nlmle.push(item.ngrossMarginSame);
            } else {
              contemporary_nlmle.push("0");
            }

            //销售数
            if (item.salesFigures) {
              theDay_xss.push(item.salesFigures);
            } else {
              theDay_xss.push("0");
            }
            if (item.salesFiguresSame) {
              contemporary_tqxss.push(item.salesFiguresSame);
            } else {
              contemporary_tqxss.push("0");
            }
            if (item.nsalesFiguresSame) {
              contemporary_nlxss.push(item.nsalesFiguresSame);
            } else {
              contemporary_nlxss.push("0");
            }

            //库存成本
            if (item.inventoryAmount) {
              theDay_kccb.push(item.inventoryAmount);
            } else {
              theDay_kccb.push("0");
            }
            if (item.invAmountSame) {
              contemporary_tqkccb.push(item.invAmountSame);
            } else {
              contemporary_tqkccb.push("0");
            }
            if (item.ninvAmountSame) {
              contemporary_nlkccb.push(item.ninvAmountSame);
            } else {
              contemporary_nlkccb.push("0");
            }
          });
          this.resultData = resultData;
          this.departmentName = departmentName;

          //销售额
          this.theDay_xse = theDay_xse;
          this.contemporary_tqxse = contemporary_tqxse;
          this.contemporary_nlxse = contemporary_nlxse;
          var xse_all = 0;
          var tqxse_all = 0;
          var nlxse_all = 0;
          theDay_xse.forEach(item => {
            xse_all += parseFloat(item);
          });
          contemporary_tqxse.forEach(item => {
            tqxse_all += parseFloat(item);
          });
          contemporary_nlxse.forEach(item => {
            nlxse_all += parseFloat(item);
          });
          this.xse_all = xse_all.toFixed(2);
          this.tqxse_all = tqxse_all.toFixed(2);
          this.nlxse_all = nlxse_all.toFixed(2);
          this.totalSum = this.xse_all;
          this.tqTotalSum = this.tqxse_all;
          this.drawLine_xse();

          //毛利额
          this.theDay_mle = theDay_mle;
          this.contemporary_tqmle = contemporary_tqmle;
          this.contemporary_nlmle = contemporary_nlmle;
          var mle_all = 0;
          var tqmle_all = 0;
          var nlmle_all = 0;
          theDay_mle.forEach(item => {
            mle_all += parseFloat(item);
          });
          contemporary_tqmle.forEach(item => {
            tqmle_all += parseFloat(item);
          });
          contemporary_nlmle.forEach(item => {
            nlmle_all += parseFloat(item);
          });
          this.mle_all = mle_all.toFixed(2);
          this.tqmle_all = tqmle_all.toFixed(2);
          this.nlmle_all = nlmle_all.toFixed(2);

          //销售数
          this.theDay_xss = theDay_xss;
          this.contemporary_tqxss = contemporary_tqxss;
          this.contemporary_nlxss = contemporary_nlxss;
          var xss_all = 0;
          var tqxss_all = 0;
          var nlxss_all = 0;
          theDay_xss.forEach(item => {
            xss_all += parseFloat(item);
          });
          contemporary_tqxss.forEach(item => {
            tqxss_all += parseFloat(item);
          });
          contemporary_nlxss.forEach(item => {
            nlxss_all += parseFloat(item);
          });
          this.xss_all = xss_all.toFixed(2);
          this.tqxss_all = tqxss_all.toFixed(2);
          this.nlxss_all = nlxss_all.toFixed(2);

          //库存成本
          this.theDay_kccb = theDay_kccb;
          this.contemporary_tqkccb = contemporary_tqkccb;
          this.contemporary_nlkccb = contemporary_nlkccb;
          var kccb_all = 0;
          var tqkccb_all = 0;
          var nlkccb_all = 0;
          theDay_kccb.forEach(item => {
            kccb_all += parseFloat(item);
          });
          contemporary_tqkccb.forEach(item => {
            tqkccb_all += parseFloat(item);
          });
          contemporary_nlkccb.forEach(item => {
            nlkccb_all += parseFloat(item);
          });
          this.kccb_all = kccb_all.toFixed(2);
          this.tqkccb_all = tqkccb_all.toFixed(2);
          this.nlkccb_all = nlkccb_all.toFixed(2);
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_tab_1_Chart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportDeptData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          const departmentName = [];
          //销售额
          const theDay_xse = [];
          const contemporary_tqxse = [];
          const contemporary_nlxse = [];
