    //画图
    drawLine_xse() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_tab1);
      let departmentName = this.departmentName;
      let theDay = this.theDay_xse;
      let contemporary = this.contemporary_tqxse;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["销售额", "同期销售额"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "0",
          left: "3%",
          right: "12%",
          bottom: "5%",
          containLabel: true
        },
        //图标 y 轴
        yAxis: {
          type: "category",
          data: departmentName,
          axisLabel: {
            formatter: function(value) {
              return value.length > 6 ? value.slice(0, 6) + "..." : value;
            }
          },
          triggerEvent: true // 设置为true后，可触发事件。实现x轴文字过长，显示省略号，hover上去显示全部的功能
        },
        //图标 x 轴
        xAxis: {
          type: "value",
          axisLabel: {
            rotate: 60
          }
        },
        //数据
        series: [
          {
            name: "销售额",
            type: "bar",
            data: theDay
          },
          {
            name: "同期销售额",
            type: "bar",
            data: contemporary
          }
        ]
      });
    },
