          containLabel: true
        },
        //图标 y 轴
        yAxis: {
          type: "category",
          data: departmentName,
          axisLabel: {
            formatter: function(value) {
              return value.length > 6 ? value.slice(0, 6) + "..." : value;
            }
          },
          triggerEvent: true // 设置为true后，可触发事件。实现x轴文字过长，显示省略号，hover上去显示全部的功能
        },
        //图标 x 轴
        xAxis: {
          type: "value",
          axisLabel: {
            rotate: 60
          }
        },
        //数据
        series: [
          {
            name: "库存成本",
            type: "bar",
            data: theDay
          },
          {
            name: "同期库存成本",
            type: "bar",
            data: contemporary
          }
        ]
      });
    }
  }
};
</script>

